.App {
	text-align: center;
    font-family : arial;
    display : flex;
    justify-content : center;
    align-items : center;
    flex-direction : column;
}


.Menu {
    width : 500px;
    height : 500px;
    border-radius : 10px;
    background-color : lightseagreen;
    display : flex;
    flex-direction : column;
    justify-content : center;
    align-items : center;

}


.Quiz {
    width : 500px;
    height : 500px;
    border-radius : 10px;
    background-color : lightseagreen;
    display : flex;
    flex-direction : column;
    justify-content : center;
    align-items : center;

}


.EndScreen {
    width : 500px;
    height : 500px;
    border-radius : 10px;
    background-color : lightseagreen;
    display : flex;
    flex-direction : column;
    justify-content : center;
    align-items : center;

}

.Menu input, button {
    width : 300px;
    height : 50px;
    margin: 5px;
    border : none;
    border-radius : 5px;
    font-size: 20px;

}

.Menu, .Quiz, .EndScreen h1 {
    color : white;
}

#nextQuestion {
    width : 200px;

}

h3 {
    color : white;
}
.App-logo {
	height: 40vmin;
	pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
	.App-logo {
		animation: App-logo-spin infinite 20s linear;
	}
}

.App-header {
	background-color: #282c34;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
}

.App-link {
	color: #61dafb;
}

@keyframes App-logo-spin {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}
